<template>
  <el-container 
  v-loading="isloading">
    <el-header 
      class = "header" 
      style = "background:#EDF2FC;">
      <el-breadcrumb 
        separator-class = "el-icon-arrow-right" 
        class = "breadcrumb">
        <el-breadcrumb-item>骑手工资计算</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <el-main>
      <el-form 
        class="small-space" 
        :model="mainForm" 
        ref="mainForm" 
        :rules="rolus"
        label-position="right" 
        label-width="110px" 
        size="small">
        <el-form-item 
          label="城市" 
          prop="GroupID">
          <el-select 
            v-model="mainForm.GroupID"  
            placeholder="请选择" 
            :clearable = "true">
            <el-option
              v-for="item in cityForm"
              :key="item.GroupID"
              :label="item.Name"
              :value="item.GroupID"/>
          </el-select>
        </el-form-item>
        <el-form-item 
          label="计算月份" 
          prop="riderReferrerDate">
          <el-date-picker v-model="mainForm.riderReferrerDate" placeholder="选择月份" type="month" format="yyyy 年 MM 月"
        value-format="yyyy-MM">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="工作天数" prop="AttendanceDaysDesigned" class="input_Day">
          <el-input v-model.number="mainForm.AttendanceDaysDesigned" placeholder='请输入工作天数'></el-input>
        </el-form-item>
        <el-form-item label="选择评价单" prop='EvaluateCsvPath' class="uploadPathForm">
          <el-col>
            <el-input v-model.trim="mainForm.EvaluateCsvPath" auto-complete="off" placeholder="请选择评价单" ref="EvaluateCsvPath" class="input-with-select" :disabled="true">
              <el-button slot="append" icon="el-icon-search" @click="handledEvaluateCsvPath"></el-button>
            </el-input>
          </el-col>
      </el-form-item>
      <el-form-item label="选择月账单" prop='CityMonthBillCsvPath' class="uploadPathForm">
          <el-col>
            <el-input v-model.trim="mainForm.CityMonthBillCsvPath" auto-complete="off" placeholder="请选择月账单" ref="CityMonthBillCsvPath" class="input-with-select" :disabled="true">
              <el-button slot="append" icon="el-icon-search" @click="handledeliveryFeeBillCsvPath" />
            </el-input>
          </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="mainCreate('mainForm')" class="userdButton">工资计算</el-button>
      </el-form-item>
    </el-form>
  </el-main>
  </el-container>
</template>
<script>
import api from '@/utils/api'
const fileDownload = require('js-file-download')
export default {
  data () {
    const validateDay = (rule, value, callback) => {
      if (!value) {
        console.log(11111)
        callback(new Error('请输入工作天数'))
        return
      }
      const ValueType = typeof (value)
      if (ValueType !== 'number') {
        callback(new Error('工作天数为数字值'))
        return
      }
      if (value < 0) {
        callback(new Error('请保持工作天数在0-31天内'))
      } else if (value > 31) {
        // console.log('哈哈')
        callback(new Error('请保持工作天数在0-31天内'))
      } else {
        callback()
      }
    }
    return {
      isloading: false,
      cityForm: [],
      mainForm: {
        CityMonthBillCsvPath: '',
        EvaluateCsvPath: '',
        GroupID: '',
        riderReferrerDate: '',
        Year: '',
        Month: '',
        AttendanceDaysDesigned: ''
      },
      rolus: {
        GroupID: [
          {required: true, message: '城市不能为空', trigge: 'change,blur'}
        ],
        AttendanceDaysDesigned: [
          { validator: validateDay, trigger: 'blur' }
        ],
        riderReferrerDate: [
          {required: true, message: '计算月份不能为空', trigge: 'change,blur'}
        ],
        EvaluateCsvPath: [
          {required: true, message: '评价单不能为空', trigge: 'change,blur'},
          {pattern: /\.(?:csv)$/i, message: '请输入正确的文件(csv)', trigge: 'change,blur'}
        ],
        CityMonthBillCsvPath: [
          {required: true, message: '月账单不能为空', trigge: 'change,blur'},
          {pattern: /\.(?:csv)$/i, message: '请输入正确的文件(csv)', trigge: 'change,blur'}
        ]
      }
    }
  },
  mounted: function () {
    this.getCity()
  },
  methods: {
    getCity () {
      this.isloading = true
      api.getCitys().then(response => {
        this.isloading = false
        this.cityForm = response.data.list
        console.log('getcity' + JSON.stringify(response.data.list))
      }, error => {
        this.isloading = false
        this.showErrorMessage(error.message)
      })
    },
    handledeliveryFeeBillCsvPath () {
      this.popupCKFinder('CityMonthBillCsvPath')
    },
    handledEvaluateCsvPath () {
      this.popupCKFinder('EvaluateCsvPath')
    },
    // popupCKFinder
    popupCKFinder (name) {
      const _this = this
    /* eslint-disable no-undef */
      try {
        CKFinder.popup({
          chooseFiles: true,
          width: 800,
          height: 600,
          onInit: function (finder) {
            finder.on('files:choose', function (evt) {
              const file = evt.data.files.first()
              _this.mainForm[name] = file.getUrl()
            })
            finder.on('file:choose:resizedImage', function (evt) {
              _this.mainForm[name] = evt.data.resizedUrl
            })
          }
        })
      } catch (e) {
        console.log(e.message)
      }
    },
    mainCreate (formName) {
      this.$refs.mainForm.validate((valid) => {
        if (valid) {
          this.isloading = true
          const riderReferrerDate = this.mainForm.riderReferrerDate
          const riderDateList = riderReferrerDate.toString().split('-')
          this.mainForm.Year = riderDateList[0]
          if (riderDateList[1][0] === '0') {
            this.mainForm.Month = riderDateList[1][1]
          } else {
            this.mainForm.Month = riderDateList[1]
          }
          const params = {
            CityMonthBillCsvPath: this.mainForm.CityMonthBillCsvPath,
            EvaluateCsvPath: this.mainForm.EvaluateCsvPath,
            GroupID: this.mainForm.GroupID,
            Year: this.mainForm.Year,
            Month: this.mainForm.Month,
            AttendanceDaysDesigned: this.mainForm.AttendanceDaysDesigned
          }
        //   console.log('提交数据' + JSON.stringify(params))
          api.calculate(params).then(response => {
            this.isloading = false
            fileDownload(response.data, 'filename.xlsx')
            this.mainForm.GroupID = ''
          }, error => {
            this.isloading = false
            this.showErrorMessage(error.message)
          })
        } else {
          this.showErrorMessage('请填写必填项')
        }
      })
    },
    showErrorMessage (message) {
      this.$message({
        message: message,
        type: 'error'
      })
    }
  }
}
</script>
<style scodived lang="scss">
 .uploadPathForm {
   position: relative;
   width: 500px;
 }
 .input_Day {
   width: 380px;
 }
</style>


